.savedDescription {
  color: var(--app-color-gray-dark);
  font-family: var(--app-font-quicksand-medium), serif;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 20px;
}

.closeIconContainer {
  position: absolute;
  top: 0;
  right: 15px;

  img {
    width: 48px;
    height: 48px;
  }
}

.contentWrapper {
  position: relative;
  display: flex;
  margin: 2% 7% 2% 7%;
}

.contentContainer {
  display: flex;
  width: 100%;
  margin-top: -20px;
  flex-direction: row;
  flex: 1
}

.content {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  flex: 4
}

.subtitleContainer {
  display: flex;
  flex: 2
}

.subtitle {
  font-family: var(--app-font-poppins), serif;
  font-size: 24px;
  line-height: 55px;
  color: var(--app-color-gray-dark);
  margin-left: 20px;
  text-align: left;
}

.componentsContainer {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  flex-wrap: wrap;
  align-content: flex-start;
}

.backIconContainer {
  // position: absolute;
  // left: -50px;
  // top: 20px;
  // right: 0;
  // bottom: 0;

  img {
    cursor: pointer;
    width: 24px;
    height: 24px;
  }
}

.fieldHistoryTitle {
  font-family: var(--app-font-quicksand-medium);
  font-size: 1rem;
  line-height: 1.2rem;
  color: var(--app-color-gray-light);
  text-align: left;
}

.componentsHost {
  padding-right: 40px;
  padding-bottom: 20px;
}