.component-card{
    padding-top: 10px;
  }
  .component-card-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: var(--app-color-white-lightest);
    border-radius: 7px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .componentTitle{
    cursor: pointer;
    font-family:  var( --app-font-quicksand-bold);
    font-size: 14px;
    font-weight: 500;
    color: var(--app-color-gray-dark);
    
  }
  .componentDescription{
    font-family:  var( --app-font-quicksand-bold);
    font-size: 14px;
    color: var(--app-color-gray-dark);
  }
  
  .clickLabel1 {
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    font-family:  var( --app-font-quicksand-bold); 
    text-align: left;
    width: 220px;
  }
  .clickLabel2 {
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    font-family:  var( --app-font-quicksand-bold); 
    text-align: center;
    width: 200px;
  }
  .clickLabel3 {
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    font-family:  var( --app-font-quicksand-bold); 
    text-align: right;
    width: 200px;
  }
  .clickLabel4 {
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    font-family:  var( --app-font-quicksand-bold); 
    text-align: left;
    width: 660px;
  }
  .tileLabel {
    cursor: pointer;
    font-size: 24px;
    font-weight: 700;
    font-family:  var( --app-font-quicksand-bold); 
  }
  .checkboxLabel{
    cursor: pointer;
    font-size: 16px;
      
  }