.task-card {
  padding-top: 10px;
}

.task-card-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: var(--app-color-white-lightest);
  border-radius: 7px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  font-weight: 700;
}

.taskTitle {
  cursor: pointer;
  font-family: var(--app-font-quicksand-bold);
  font-size: 14px;
  font-weight: 500;
  color: var(--app-color-gray-dark);

}

.taskDescription {
  font-family: var(--app-font-quicksand-bold);
  font-size: 14px;
  color: var(--app-color-gray-dark);
}

.clickLabel {
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
}


.checkboxLabel {
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
}