.host {
  display: flex;
  flex-direction: column;
  padding-right: 30px;
}

.dashboardPanel {
  display: flex;
  flex-direction: column;
  //background-color: #F1F1F1;
  border-radius: 7px;
  padding: 10px 10px 100px 10px;
}

.headerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 30px
}

.title {
  font-family: var(--app-font-poppins);
  font-size: 1.5rem;
  line-height: 1.5rem;
  color: var(--app-color-gray-dark);
}

.modalTitle {
  font-family: var(--app-font-poppins);
  font-size: 2rem;
  color: var(--app-color-gray-dark);
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center
}

.singleButton {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  outline: none;
  cursor: pointer;
  font-family: var(--app-font-quicksand-medium);
  padding: 10px;
  margin: 10px 10px 10px 0px;
  height: 40px;
  overflow: hidden;
  border: 1px solid var(--app-color-gray-lighter);
  background-color: var(--app-color-white-lightest);
  color: var(--app-color-gray-dark);
  stroke: var(--app-color-gray-dark);
  width: 120px;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: 10px;
  height: 32px;
  flex: 1 1;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-font-quicksand-medium);
    font-size: 1rem;
    color: var(--app-color-red-error);
  }

  outline: none;
  border: none;
  padding-left: 10px;
  border: 0px;
  min-width: 350px;
  width: 100%;
  height: 37px;
  font-family: var(--app-font-quicksand-semibold);
  font-size: 1rem;
  color: var(--app-color-gray-dark);
}

.dashboardCard {
  min-height: 50px;
  background-color: var(--app-color-white-lightest);
  cursor: pointer;
  width: 12%;
  min-width:140px;
  margin: 0px 15px 0px 15px;
  border-radius: 8px;
  border: 1px dashed var(--app-color-gray-light);
  padding: 10px 20px 20px 20px;
  font-weight: 100;
  font-size: 18px;
  text-align: center;
  p {
    text-align: center;
    font-size: 22px;
    color: var(--app-color-gray-dark);
    min-height: 40px;
  }
}


